import CodePage from "@/app/page/code/page";
import Certificate from "./Certificate";

export default function Skills() {
  const technical = [
    {
      name: "HTML",
      src: "/icons/html.png",
    },
    {
      name: "CSS",
      src: "/icons/css.png",
    },
    {
      name: "JavaScript",
      src: "/icons/javaScript.png",
    },
    {
      name: "React",
      src: "/icons/react.png",
    },
    {
      name: "Next",
      src: "/icons/next.png",
    },
  ];

  const tools = [
    {
      name: "Figma",
      src: "/icons/figma.png",
    },
    {
      name: "VS Code",
      src: "/icons/vscode.png",
    },
    {
      name: "Tailwind CSS",
      src: "/icons/tailwindcss.png",
    },
    {
      name: "Bootstrap",
      src: "/icons/bootstrap.png",
    },
    {
      name: "GitHub",
      src: "/icons/Github.png",
    },
    {
      name: "Postman",
      src: "/icons/postman.webp",
    },
  ];

  const soft = {
    list01: "Management Skills",
    list02: "Decision Making",
    list03: "Flexibility and Adalitability",
    list04: "Service Mind",
    list05: "Teamwork",
    list06: "Critical Thinking",
  };

  return (
    <>
      {/* name of each tab group should be unique */}
      <div className="tabs tabs-lift h-screen content-center animate-viewpoint">
        <input
          type="radio"
          name="my_tabs_3"
          className="tab btn btn-sm btn-outline"
          aria-label="SKILLS"
          defaultChecked
        />
        <div className="tab-content p-8 rounded-b-lg border-b-2 border-primary bg-primary">
          <div className="grid lg:grid-cols-6 gap-8 lg:gap-12">
                      {/* Card 1 */}
          <div className="card-skills">
            <p className="card-skills-title">
              TECHNICAL SKILLS
            </p>
            <ul className="card-skills-content">
              {technical.map((skill, i) => (
                <li key={i} className="contact">
                  <img
                    className="h-8 w-8 icon invert"
                    src={skill.src}
                    alt={skill.name}
                  />
                  {skill.name}
                </li>
              ))}
            </ul>
          </div>

          {/* Card 2 */}
          <div className="card-skills">
            <p className="card-skills-title">
              TECHNICAL SKILLS
            </p>
            <ul className="card-skills-content">
              {tools.map((tool, i) => (
                <li key={i} className="contact">
                  <img
                    className="h-8 w-8 icon invert"
                    src={tool.src}
                    alt={tool.name}
                  />
                  {tool.name}
                </li>
              ))}
            </ul>
          </div>

          {/* Card 3 */}
          <div className="card-skills">
            <p className="card-skills-title">
              TECHNICAL SKILLS
            </p>
            <ul className="card-skills-content">
              <li>{soft.list01}</li>
              <li>{soft.list02}</li>
              <li>{soft.list03}</li>
              <li>{soft.list04}</li>
              <li>{soft.list05}</li>
              <li>{soft.list06}</li>
            </ul>
          </div>
          </div>

        </div>

        <input
          type="radio"
          name="my_tabs_3"
          className="tab"
          aria-label="PROJECTS"
        />
        <div className="tab-content p-6">
        <CodePage/>
        </div>

        <input
          type="radio"
          name="my_tabs_3"
          className="tab"
          aria-label="CERTIFICATIONS"
        />
        <div className="tab-content p-6">
          <Certificate/>
        </div>
      </div>
    </>
  );
}
